import uniRate from '@/components/uni-rate/uni-rate.vue'
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
import uParse from '@/components/gaoyia-parse/parse.vue'
 import topicon from '@/components/gwh-backTopIcon/gwh-backTopIcon.vue'
export default{
	data(){
		return{
			top:0,
			active:1,
			msg:[
				{length:3,
				videoSrc:'',
				},
				{length:0,
				videoSrc:'http://workers.lllnet.cn/video/gclc-techs/CYGR20201581243698-3.mp4?sign=22d05d9296b3ea0bbeb2a455ecc49212&t=5e846548',
				},
				{length:2,
				videoSrc:'',
				}
			],
			sh:'',
			dataList:[],
			page:1,
			pageSize:10,
			total:0,
			more:''
		}
	},
	components:{
		uniRate,
		uniLoadMore,
		uParse,
		topicon
	},
	computed: {
	     noMore () {
	       return this.dataList.length >= this.total 
	     },
		 showIcon(){
		 				 return this.top>=50
		 }  
	   },
	   onReachBottom(){
		   if(this.noMore){
		   					this.more='noMore'
		   					return;
		   				}
		   				var that=this
		   				this.more='loading'
		   // setTimeout(function(){
		   				  that.page++
		   				  that.apart()
		   // },2000)
	   },
	   mounted(){
	   	var that=this
	   	this.apart()
	   	setTimeout(function(){
	   		that.$getHeight('#sv',(res) =>{
	   			that.sh=res
	   		})
	   	},0)
	   },
	   onPageScroll(e){
		 this.top=e.scrollTop
	   },
	methods:{
		toDetail(item){
			uni.navigateTo({
				url:'/pages/index/articleDetail?id='+item.id+'&like='+item.like+'&comment='+item.comment+'&repost='+item.repost
			})
		},
		toShop(id){
			uni.navigateTo({
				url:`/pages/bussiness/shopPreview?id=${id}`
			})
		},
		toGoodsDetail(id){
			uni.navigateTo({
				url:`/pages/index/goodsDetail?id=${id}`
			})
		},
		toggle(t){
			this.active=t
			this.reset()
			this.apart()
		},
		reset(){
			this.page=1
			this.total=0
			this.dataList=[]
			this.more=''
		},
		apart(){
			var that=this,params={
				page:this.page,
				pagesize:this.pageSize
			},url
			if(this.active==1){
				url='&r=api.college.hotarticle'
				params.uid=1182
			}else if(this.active==2){
				url='&r=api.college.merchant&&page='+this.page+'&pagesize='+this.pageSize
			}else if(this.active==3){
				url='&r=api.college.goods&&page='+this.page+'&pagesize='+this.pageSize
			}
			// else if(this.active==2){
			// 	var url='&r=api.discovery.goods'
			// 	var params={
			// 		pagesize:that.pageSize,
			// 		page:that.page
			// 	}
			// 	that.getList(params,url)
			// }
			that.getList(params,url)
		},
		getList(p,url){
			var that=this
			if(this.page==1){
				this.$loading()
			}
			  this.$apiPost(url,p).then((res) =>{
				  that.total=Number(res.total)
				  if(that.active==1){
					   that.dataList=that.dataList.concat(res.data.map((item) =>{
						   item.detail=item.detail.replace(/\<img/gi, '<img class="richImg"')
						   return item
					   }))
				  }else{
					   that.dataList=that.dataList.concat(res.data)
				  }
				 
				  that.more=''
				  if(that.page==1){
				  	uni.hideLoading()
				  }
			  })
		},
		toBottom(){
			if(this.noMore){
				this.more='noMore'
				return;
			}
			var that=this
			this.more='loading'
		  // setTimeout(function(){
			  that.page++
			  that.apart()
		  // },2000)
		},
		showImgPreview(list,cur){
			uni.previewImage({
				current:cur,
				urls:list
			})
		},
		tapIcon(){
			uni.pageScrollTo({
				scrollTop:0,
				duration:300
			})
		}
	}
}
